import React, { ReactNode, useState } from "react"
//import { style } from "glamor"

const Wifi = ({
                index, 
                style,
                styles,
                triggerButtonAfterIcon,
                hoverStatus,
                //updateActiveId,
                textActive,
                title,
                //activeSelect, 
                //updates, 
                onClickFunc, 
                activeTabName,
                activeSelectionIcon,
               // floatF, 
                MouseOver,
                MouseOff
                    }: any) => {

    
  

    return (
        // <div>Wifi</div>
    <>
    <li key={index}>
        {hoverStatus.index === index && hoverStatus.status === true && 
        //{activeSelect.activeTabId === index && // updateActiveId(index) */}
        <span className="wifi-symbol" {...style(styles["wifi-symbol"])}>
            <div className="wifi-circle first" {...style(styles["wifi-circle first"])}></div>
            <div className="wifi-circle second" {...style(styles["wifi-circle second"])}></div>
            <div className="wifi-circle third" {...style(styles["wifi-circle third"])}></div>	
            <div className="wifi-circle fourth" {...style(styles["wifi-circle fourth"])}></div> 
        </span>  }
        <span key={index} {...style(styles['options-container'])} className={activeTabName(index)} onMouseEnter={() => MouseOver(index)}
          onMouseLeave={() => MouseOff(index)}>
            <span className="material-symbols-sharp-container" {...style(styles['material-symbols-sharp-container'])} style={{ animation: "fadeIn 800ms linear" }}>
                <link rel="stylesheet" href="https://fonts.googleapis.com/css2?family=Material+Symbols+Sharp:opsz,wght,FILL,GRAD@48,400,1,0" />
            {/* <span className="material-symbols-sharp-container" {...style(styles['material-symbols-sharp-container'])} style={{ animation: "fadeIn 800ms linear" }}> */}
                    <span className={activeSelectionIcon(index)} {...style(styles['material-symbols-sharp change'])} {...style(styles['material-symbols-sharp change selected'])}>       
                        {triggerButtonAfterIcon}
                    </span>
                </span>
            <span className="text" {...style(styles['text'])} {...style(styles['text'])}>
                <div onClick={() => onClickFunc(index)}>{title.title}</div>
                </span>
        </span> 
    </li>
    </>
    
    )
}

export { Wifi }



