import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
  ComponentProps
} from "streamlit-component-lib";
import React, { ReactNode, useState } from "react";
import { style } from "glamor";
import { Wifi } from "./wifi";
import "./icon.css";
import "./style.scss";

function CustomButton (props: ComponentProps) {

  const { args } = props
  const titles:any[] = args["titles"] 
  const triggerButtonIcon:string = args["triggerButtonIcon"]
  const triggerButtonAfterIcon:string = args["triggerButtonAfterIcon"]
  const styles:any = args['styles'] || {}

  const [newTemplate, setNewTemplate] = useState(titles)   
  const [clickedB, setClickedB] = useState(false);
  // const [newButton, setNewButton] = useState(false);
  const [hoverStatus, setHoverStatus] = useState({index: 0, status: false});
  const [activeSelect, setActiveSelect] = useState({activeTabId: 0})


  const MouseOver = (index: number) => {
    setHoverStatus({ index: index, status: true });
    console.log(hoverStatus);
  };

  const MouseOff = (index: number) => {
    setHoverStatus({ index: index, status: false });
    console.log(hoverStatus);
  };

  const updateActiveId = (index:number): void => {
    
    setActiveSelect({activeTabId: index})
    console.log(index, activeSelect)

    let output = newTemplate[index]
    Streamlit.setComponentValue({index:index, clickedTab:output})
    
  };

  const activeTabName = (id:number) => {
    if (id === activeSelect.activeTabId) {
      return "options-container active";
    } else {
      return "options-container";
    }
  };  

  const activeSelectionIcon = (index:number) => {
    if (hoverStatus.index === index && hoverStatus.status === true) {
      return "material-symbols-sharp change selected";
    } else {
      return "material-symbols-sharp change"
    }
  }
  
  const clickedFunc = (): void => {
    if (clickedB === true) {
      setClickedB(false);
    } else {
      setClickedB(true);
    }

    // console.log(newButton);
  };

  const textActive = (id:number) => {
    if (id === activeSelect.activeTabId) {
      return "text active";
    } else {
      return "text";
    }
  }

  return (
    <div className="component" {...style(styles["component"])}>
      <span className="new-button-component" {...style(styles["new-button-component"])}>
        <span className="button-container" {...style(styles["button-container"])} onClick={() => clickedFunc()}>
          <span className="button-icon-container" {...style(styles["button-icon-container"])}>
            <button className="new-template-button" {...style(styles["new-template-button"])}>
            {clickedB === false ? (
                <span className="icon-container" {...style(styles["icon-container"])}>
                  <i className="material-icons" id="add-btn" {...style(styles["add-btn"])}>
                    {triggerButtonIcon}
                  </i>
                </span>
              ) : (
                <>
                  <link rel="stylesheet" href="https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@48,400,0,0"/>
                  <span className="material-symbols-outlined off" {...style(styles["material-symbols-outlined off"])}>
                    mode_off_on
                  </span>
                </>
              )}
            </button>
          </span>
        </span>
        {clickedB === true && (
          <span className="options-around-button" {...style(styles["options-around-button"])}>
            <ul className="option-indicator-one" {...style(styles["option-indicator-one"])}>
              {newTemplate.map((title, index) => (
                <>
                  <Wifi
                    index={index}
                    style={style}
                    styles={styles}
                    triggerButtonAfterIcon={triggerButtonAfterIcon}
                    //updateActiveId={updateActiveId}
                    textActive={textActive}
                    title={title}
                    hoverStatus={hoverStatus}
                    activeSelect={activeSelect}
                    onClickFunc={updateActiveId}
                    activeTabName={activeTabName}
                    activeSelectionIcon={activeSelectionIcon}
                    MouseOver={MouseOver}
                    MouseOff={MouseOff}
                  />
                </>
              ))}
            </ul>
          </span>
        )}
      </span>
    </div>
  );
}


export default withStreamlitConnection(CustomButton)
